/* eslint-disable @typescript-eslint/ban-ts-comment */
import * as React from "react";
import { Module } from "@types";

interface SystemProps {
  module: Module | null;
}

interface DynamicScriptProps {
  url: string | null | undefined;
}

/**
 *
 * @param args gets the url of the service we need to show
 */
export const useDynamicScript = (args: DynamicScriptProps): { ready: boolean; failed: boolean } => {
  const [ready, setReady] = React.useState<boolean>(false);
  const [failed, setFailed] = React.useState<boolean>(false);
  React.useEffect(() => {
    if (!args.url) {
      return;
    }
    const element = document.createElement("script");
    element.src = args.url;
    element.type = "text/javascript";
    element.async = true;

    setReady(false);
    setFailed(false);

    element.onload = () => {
      setReady(true);
    };

    element.onerror = () => {
      setReady(false);
      setFailed(true);
    };

    document.head.appendChild(element);

    return () => {
      document.head.removeChild(element);
    };
  }, [args.url]);

  return { ready, failed };
};

/**
 * loads remote component as react component
 * @param scope name of the application we are trying to fetch
 * @param module module of that application that is public
 */
const loadComponent = (scope: string, module: string) => async (): Promise<{
  default: React.ComponentType<any>;
}> => {
  //@ts-ignore
  await __webpack_init_sharing__("default");
  //@ts-ignore
  const container = window[scope];

  //@ts-ignore
  await container.init(__webpack_share_scopes__.default);

  //@ts-ignore
  const factory = await window[scope].get(module);
  //@ts-ignore
  // const routes = await window[scope].get("./Route");
  // console.log(routes().default);
  const Module = factory();
  return Module;
};

const System: React.FC<SystemProps> = (props) => {
  if (!props.module) {
    return <h1>No System Specified</h1>;
  }

  // eslint-disable-next-line react-hooks/rules-of-hooks
  const { ready, failed } = useDynamicScript({ url: props.module && props.module.url });

  if (!ready) {
    return <h1>Loading...</h1>;
  }

  if (failed) {
    return <h2>Failed to load {props.module!.scope}</h2>;
  }

  const Component = React.lazy(loadComponent(props.module!.scope, props.module!.module));

  return (
    <React.Suspense fallback="loading system">
      <Component />
    </React.Suspense>
  );
};

export default System;
