import * as React from "react";
import { Switch, Route, useHistory, Redirect, useLocation } from "react-router-dom";
import {
  ThemeProvider,
  StylesProvider,
  jssPreset,
  makeStyles,
  Theme,
  createStyles,
} from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import Button from "@material-ui/core/Button";
import { create } from "jss";
import rtl from "jss-rtl";
import { useTranslation } from "react-i18next";
import { SnackbarProvider } from "notistack";
import Home from "./pages/home";
import Login from "./pages/login";
import { useSelector } from "react-redux";
import { ErsState} from "./state";
import { SeamlessTheme } from "@seamless/ui";
import { loadState, saveState } from "@seamless/common";


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    success: { backgroundColor: theme.palette.success.main },
    error: { backgroundColor: theme.palette.error.main },
    warning: { backgroundColor: theme.palette.warning.main },
    info: { backgroundColor: theme.palette.info.main },
  })
);

const App: React.FC = () => {
  const { i18n } = useTranslation();
  const jssConfig = { plugins: [...jssPreset().plugins, rtl()] };
  const jss = create(jssConfig);
  const classes = useStyles();
  const { t } = useTranslation();
  const notistackRef = React.createRef<SnackbarProvider>();

  let sessionid = new URLSearchParams(location.search).get("sessionid");
  if (sessionid) {
    saveState(sessionid, "sessionid");
  }
  const dismissNotification = (key: string | number) => () => {
    if (!notistackRef.current) {
      return;
    }
    notistackRef.current.closeSnackbar(key);
  };
  const isAuth = useSelector<ErsState>((state) => state.auth.isAuth);

  return (
    <StylesProvider jss={jss}>
      <ThemeProvider theme={SeamlessTheme}>
        <CssBaseline />
        <div dir={i18n.dir(i18n.language)}>
          <SnackbarProvider
            anchorOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            autoHideDuration={4000}
            classes={{
              variantSuccess: classes.success,
              variantError: classes.error,
              variantWarning: classes.warning,
              variantInfo: classes.info,
            }}
            dense={false}
            ref={notistackRef}
            action={(key) => (
              <Button color="inherit" onClick={dismissNotification(key)}>
                {t("dismiss")}
              </Button>
            )}
          >
            <Route path="/home">
              <Home />
            </Route>
            <Route>
              <Redirect to="/home" />
            </Route>
          </SnackbarProvider>
        </div>
      </ThemeProvider>
    </StylesProvider>
  );
};

export default App;
