/** @format */

import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import XHR from "i18next-xhr-backend";
import LanguageDetector from "i18next-browser-languagedetector";
import Backend from "i18next-chained-backend";

const becomeInternational = (location: string, language: string) => {
  i18n
    .use(Backend)
    .use(initReactI18next)
    .use(LanguageDetector)
    .init({
      backend: {
        backends: [XHR],
        backendOptions: [
          {
            loadPath: location,
          },
        ],
      },
      debug: false,
      defaultNS: "messages",
      fallbackLng: "en",
      interpolation: {
        escapeValue: false,
      },
      lng: language,
      ns: ["messages"],
    });
  return i18n;
};

export default becomeInternational;
